.movieDetail__container {
  padding: 200px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.movieDetail__infos {
  display: flex;
  flex-direction: column;
}

.movieDetail__header {
  display: flex;
  align-items: center;
  font-weight: 200;
}

.movieDetail__title {
  margin-right: 5px;
  font-size: 20px;
}

.movieDetail__genres {
  display: flex;
  list-style: none;
  padding: 0;
  gap: 5px;
}
.movieDetail__genre {
  font-weight: 100;
  opacity: 0.8;
}
.goBack {
  padding: 15px 5px;
  background-color: white;
  border: none;
  font-size: 20px;
  font-weight: 600;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  &:hover {
    background-color: rgba(0, 0, 0, 0.2);
    color: white;
  }
}
